import React, {useEffect, useState} from 'react'
import { Link } from 'react-router-dom'
import firebase from 'firebase'
import {
  CButton,
  CCard,
  CCardBody,
  CCardGroup,
  CCol,
  CContainer,
  CForm,
  CInput,
  CInputGroup,
  CInputGroupPrepend,
  CInputGroupText,
  CTextarea,
  CRow
} from '@coreui/react'
import CIcon from '@coreui/icons-react'
var firebaseConfig = {
  apiKey: "AIzaSyD5rgceH1hoLoIyG6ELVXeN2jzEPHECQVg",
  authDomain: "login-laporan-rapat.firebaseapp.com",
  databaseURL: "https://login-laporan-rapat.firebaseio.com",
  projectId: "login-laporan-rapat",
  storageBucket: "login-laporan-rapat.appspot.com",
  messagingSenderId: "364342812861",
  appId: "1:364342812861:web:8cc2310c9cb0dc309017d4"
};
// Initialize Firebase
var firebasu = firebase.initializeApp(firebaseConfig);




const Login = () => {
  const [nourut, setNourut] = useState();
  const [value, setValue] = useState();


  useEffect(() => {
      firebasu.database().ref('nomerurut').once("value").then(function (snapshot){ 
          setNourut(snapshot.val())
      })
    }
  )

  const handleChange = (e) => {
    setValue({nourut: nourut, value: "N", komentar : e.target.value})
  }
  const handleClick = (e) => {
    var updates = {};
    var angka = nourut + 1
    updates['/nomerurut/'] = angka;
    firebase.database().ref().update(updates)
    firebase.database().ref('data/' + nourut).set({
      flag: value.value,
      komentar: value.komentar
    });
  }

console.log(value)
  return (
    <>
      <CRow className="pb-4">
        <CCol xs="12" sm="6" md="6" lg="6">  
        <div class="embed-responsive embed-responsive-16by9">
        <iframe src="https://www.youtube.com/embed/tQ2ooQ2VIa4" allowfullscreen></iframe>
        </div>  
        </CCol>
      </CRow>
      <CRow>
      <CCol xs="12" sm="6" md="6" lg="6">  
        <div className="pb-2"><CTextarea onChange={handleChange.bind(this)} ></CTextarea></div>
        <CButton onClick={handleClick.bind(this)} color="info">CETAKKEUN</CButton>  
      </CCol>    
      </CRow>     
    </>
  )
}

export default Login
